/* page specific */

.news-card {
    position: relative;
    float: left;
    padding: 0 30px;
    width: 500px;
    margin-bottom: 30px;

}

@media only screen and (max-width: 61.25em) {
    .news-card {
        width: 100%
    }
}

/*-- news-card__content */
.news-card__content {
    margin: 0;
    padding: 0 15px;
    list-style-type: none;
}

.news-card__content::before, .news-card__content::after {
    content: '';
    display: table;
}

.news-card__content::after {
    clear: both;
}

.news-card__content li {
    display: inline-block;
}

.news-card__content a {
    color: #fff;
}

/*-- news-card__img */
.news-card__img {
    position: absolute;
    z-index: 1;
    width: calc(100% - 60px);
    height: 100%;
    filter: brightness(70%);
    transition: filter .15s ease-in;
    object-fit: cover;
}

.news-card:hover .news-card__img {
    filter: brightness(40%);
}

/*-- news-card__wrapper */
.news-card__wrapper {
    padding: 0 15px;
    background-color: #fff;
    min-height: 400px;
    position: relative;
    overflow: hidden;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.2);
}

.news-card__wrapper:hover .news-card__data {
    transform: translateY(0);
}

.news-card__data {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    transform: translateY(calc(70px + 1em));
    transition: transform 0.3s;
}

.news-card__month {
    text-transform: uppercase;
}

.news-card__month, .news-card__year {
    font-size: 12px;
}

.news-card__header {
    padding: 25px 0;
    color: #fff;
}

.news-card__author {
    font-size: 12px;
}

.news-card__title {
    margin-top: 10px;
    margin-bottom: 20px;
    font-weight: 300;
}

.news-card__description {
    height: 70px;
    line-height: 1.3rem;
    margin: 0;
}

.news-card input[type='checkbox'] {
    display: none;
}

.news-card input[type='checkbox']:checked + .news-card__content {
    transform: translateY(-60px);
}

.news-card__wrapper {
    background-color: transparent;
    z-index: 2;
}

.news-card__wrapper:hover .news-card__content span {
    transform: translate(-50%, -10px);
    opacity: 1;
}

.news-card__header::before, .news-card__header::after {
    content: '';
    display: table;
}

.news-card__header::after {
    clear: both;
}

.news-card__date {
    float: left;
    font-size: 12px;
}

.news-card__content {
    float: right;
}

.news-card__content li {
    margin: 0 5px;
    position: relative;
}

.news-card__content span {
    transition: all 0.3s;
    opacity: 0;
}

.news-card__data {
    color: #fff;
    transform: translateY(calc(70px + 4em));
}

.news-card__title a {
    color: #fff;
}

.news-card__button {
    display: block;
    width: 100px;
    margin: 10px auto 40px;
    text-align: center;
    font-size: 12px;
    color: #fff;
    position: relative;
    font-weight: 700;
}

.news-card__button::after {
    content: '\2192';
    opacity: 0;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(0, -50%);
    transition: all 0.3s;
}

.news-card__button:hover::after {
    transform: translate(5px, -50%);
    opacity: 1;
}



